}

.timeline-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 50%;
  background-color: black;
  align-self: flex-start;
  -webkit-clip-path: polygon(93% 37%, 100% 50%, 93% 63%, 93% 100%, 0 100%, 0 0, 93% 0);
          clip-path: polygon(93% 37%, 100% 50%, 93% 63%, 93% 100%, 0 100%, 0 0, 93% 0);
  padding: 2%;
  color: #fbfcf8;
}
